@import "../../style/utils";

nav {
  height: 100%;
  @include responsiveMobiles {
    width: 100%;
  }

  ul {
    @include flexRow;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
  }
  li {
    height: 100%;
    @include responsiveMobiles {
      width: 50%;
    }
    a {
      padding: 0 1.5rem;
      @include responsiveMobiles {
        padding: 0 0.5rem;
      }
    }
  }
  li:nth-child(1) {
    a {
      background-color: map-get($colors, darkmint);
      color: map-get($colors, ice);
      font-size: 1.3rem;
      font-weight: 700;
      transition: all 300ms ease-in-out;
      &:hover {
        background-color: lighten(map-get($colors, mint), 0%);
        color: map-get($colors, mauve);
      }
      @include responsiveMobiles {
        height: 60px;
        font-size: 1.1rem;
        font-weight: 500;
        font-style: normal;
      }
    }
  }
  li:nth-child(2) {
    a {
      background-color: map-get($colors, rose);
      color: map-get($colors, ice);
      font-size: 1.1rem;
      font-style: italic;
      transition: all 300ms ease-in-out;
      &:hover {
        background-color: lighten(map-get($colors, rose), 20%);
        color: map-get($colors, mauve);
      }
      @include responsiveMobiles {
        height: 60px;
        font-size: 1.1rem;
        font-weight: 500;
        font-style: normal;
      }
    }
  }
}
